@import "tools.scss";

$primary-color: #066AFF;
$primary-light-color: #e7f0ff;
$danger-color: #ff4136;
$grey-background: rgba(black, .1);

body, html{
	margin: 0;
	padding: 0;
	text-decoration: none;
	font-family: sans-serif;
}

hr{
	position: relative;

	display: block;
	border: 0;

	height: 2px;
	width: 100%;
	background: rgba(black, .1);

	margin: 30px 0;
}

.ellipsis{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.horizontal-scroller{
	display: flex;
	// justify-content: center;
	box-sizing: border-box;
	flex-wrap: nowrap;
	overflow-x: hidden;
	width: 100%;

	& > *{
		flex: 0 0 auto;
	}
}

.card-5{
  position: relative;
  @include flex(flex, column, center, center, center);
  margin: 10px 0;
  // margin-top: 50px;
  & > *{
    text-align: center;
  }
  small{
    margin:0 0 20px 0;
    font-size: 16px;
  }
  .image{
    position: relative;
    width: 800px;
    &.md{
      width: 400px;
    }
    &.max{
      width: 100%;
    }
    overflow: hidden;
    img{
      @extend %image;
    }
  }
}

.logo{
  position: relative;
  @include flex(flex, row, null, center);
	img{
		position: relative;
		display: block;
		height: 50px;
		width: auto;
	}
}

.sec-title{
		@extend %title;
    text-align: center;
}

.to-bottom{
	position: absolute;
	bottom: 10px;
}

.btn-outline-primary{
	@include btn( $primary-color, rgba($primary-color, .1), transparent, 3px );
}

.parser{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 0;
  span{
    position: relative;
    display: block;
    padding: 0 5px;
    background: white;
    z-index: 2;
  }
  width: 100%;
  font-weight: bold;
  color: grey;
  &:before, &:after{
    position: relative;
    display: block;
    content: ' ';
    background: $grey-background;
    height: 2px;
    width: calc(50% - 20px);
  }
  &.hg{
    &:before, &:after{
      width: calc(50% - 80px);
    }
  }


}

.btn-flat{
    width: 100%;
}

.btn-light{
	@include btn( $primary-color, white, transparent, null )
}

.btn-primary{
	@include btn( white, $primary-color, $primary-color, 3px );
	&:before{
		content:'';
		position:absolute;
		left: 0;
		top: 0;
		width: 0;
		height: 100%;
		transition: .2s;
		background: rgba(white, .1);
		transition: .2s width;
		z-index: 2;
	}

	&:hover:before{
		width: 100%;
	}

  &.hide{
    display: none;
  }
  &.loading:before{
    width: 30%;
    background: rgba(white, .1);
    animation: btnBeforeLoading 2s infinite;
  }
  &.loading:hover:before{
    width: 30%;
  }

}

.btn-danger{
	@include btn( white, $danger-color, $danger-color, 2px );
	&:before{
		content:'';
		position:absolute;
		left: 0;
		top: 0;
		width: 0;
		height: 100%;
		transition: .2s;
		background: rgba(white, .1);
		transition: .2s width;
		z-index: 2;
	}

	&:hover:before{
		width: 100%;
	}

  &.hide{
    display: none;
  }
  &.loading:before{
    width: 30%;
    background: rgba(white, .1);
    animation: btnBeforeLoading 2s infinite;
  }
  &.loading:hover:before{
    width: 30%;
  }

}

.btn-full{
	display: block;
	width: 100%;
	text-align: center;
}

.container-info{
  position: relative;
  display: block;
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 3px;
  border: 1px solid $grey-background ;
  color: $primary-color;
  background: white;

  &.active{
    border-color: $primary-color;
		background: $primary-light-color;
  }
  & > *{
    line-height: 16px;
    display: block;
  }
  .notes{
    margin-top: 10px;
    padding-top: 5px;
    border-top: 1px solid $primary-color;
    font-weight: normal;
  }
  &.notify{
    position: fixed;
    background: $primary-color;
    color: white;
    top: 10px;
    width: 400px;
    left: calc(50% - 200px);
    animation: fadeInDown .2s;
    // right: calc(100% - 150px);

    @include box-shadow($md-shadow);
    z-index: 100;
    font-weight: bold;
    .notes{
      // border-color: white;
    }

  }
}

.search{
  @include flex(flex, column, center, flex-start, flex-start);
  animation: fadeInUp .2s;
  --content-width: 600px;
  $content-width: var(--content-width);
  height: 100vh;
  box-sizing: border-box;
  padding-top: 20px;
  h1{
    position: relative;
    display: block;
    text-align: center;
    font-size: 40px;
    font-weight: bold;
    margin: 20px 0 40px 0;
  }
  .btn-close{
    @include btn( $primary-color, rgba($primary-color, .1), transparent, 3px );
    position: absolute;
    font-size: 26px;
    width: 60px;
    height: 60px;
    border-radius: 50px;
    padding: 0;
    right: 25px;
    top: 25px;
    z-index: 2;
    cursor: pointer;
    // &:active{
    //   @include box-shadow(0px 0px 17px -3px rgba(117,117,117,0.14));
    // }
  }
  .form{
    position: relative;
    @include flex(flex, row, center, flex-start, flex-start);
    @include placeholder( rgba(black, .6), 18px );
    width: $content-width;
    height: auto;
    input{
      position: relative;
      box-sizing: border-box;
      background: white;
      width: 100%;
      padding: 15px 50px 15px 15px;
      font-size: 18px;
      outline: 0;
      border: 2px solid whitesmoke;
      border-radius: 5px;
      transition: .2s border;
      @include box-shadow(0px 0px 17px -3px rgba(117,117,117,0.14));
      &:focus{
        border-color: $primary-color;
      }
    }
    .btn{
      @include btn( rgba(black, .4), transparent, transparent, 3px );
      position: absolute;
      font-size: 16px;
      width: 45px;
      height: 45px;
      margin: 0 5px;
      padding: 0;
      right: 0;
      z-index: 2;
      cursor: pointer;
      &.active{
        background: rgba($primary-color, .1);
        color: $primary-color;
      }
      &:active{
        @include box-shadow(0px 0px 17px -3px rgba(117,117,117,0.14));
      }
      &:nth-child(1){
        left: 0;
      }
    }
  }
  .options{
    position: relative;
		// top: 100%;
		max-height: 600px;
		overflow: auto;
    @include flex(flex, column, flex-start, flex-start, flex-start);

    width: $content-width;
    background: white;
    box-sizing: border-box;
    padding: 10px;
    margin-top: 10px;
    border: 2px solid whitesmoke;
    border-radius: 5px;
    @include box-shadow(0px 0px 17px -3px rgba(117,117,117,0.14));
		z-index: 5;

		&.hide{
			display: none;
		}

    a{
      position: relative;
      text-decoration: none;
      font-weight: bold;
      font-size: 16px;
      padding: 10px;
      box-sizing: border-box;
      color: black;
      border-bottom: 1px solid whitesmoke;
      justify-content: flex-start;
      width: 100%;
      &:last-child{
        border: 0;
      }
      &:hover{
        background: #f9f9f9;
      }
      &:active{
        background: whitesmoke;
      }
    }

  }
}

.card-user{
  position: relative;
  @include flex( flex, row, center, flex-end, flex-start );
  text-decoration: none;
  color: black;
  box-sizing: border-box;
  background: white;
  border-radius: 5px;
  margin: 5px 0 5px 0;
  // @include box-shadow($sm-shadow);

  .image{
    position: relative;
    display: block;
    width: 50px;
    height: 50px;
    box-sizing: border-box;
    border-radius: 100px;
    overflow: hidden;
    border: 2px solid white;
    background: $grey-background;
    @include box-shadow($sm-shadow);

    img{
      @extend %image;
    }
  }
  .details{
    position: relative;
    display: block;
    padding: 0 0 0 10px;
    box-sizing: border-box;
    max-width: 100px;
    overflow: hidden;
    & > *{
      display: block;
    }
    small{
      font-weight: bold;
      padding: 0 0 2.5px 0;
      font-size: 12px;
      color: grey;
    }
    .name{
      text-decoration: none;
      margin: 0 0 2.5px 0;
      font-size: 14px;
      text-transform: capitalize;
    }
  }
  &.hg{
    justify-content: center;
    max-width: 400px;
    margin: 20px 0;
    .image{
      width: 100px;
      height: 100px;
    }
    .details{
      padding-left: 20px;
      max-width: calc(100% - 100px);
    }
    .name{
      margin: 0 0 10px 0;
    }
    small{
      margin: 0 0 5px 0;
      font-size: 14px;
    }
  }
}

.card-2{
  position: relative;
  @include flex( flex, row, center, flex-start, flex-start );
  background: white;
  border-radius: 5px;
  padding: 10px;
  border: 1px solid $grey-background;

  .image{
    position: relative;
    width: 60px;
    height: 60px;
    border-radius: 50px;
    overflow: hidden;
    border: 5px solid white;
    background: $grey-background;
    @include box-shadow(0px 0px 36px -23px rgba(87,87,87,1));
    img{
      @extend %image;
    }
  }
  .details{
    position: relative;
    display: block;
    box-sizing: border-box;
    overflow: hidden;
    width: auto;
    padding: 0 0 0 10px;
    & > *{
      display: block;
    }
    small{
      font-weight: normal;
      padding: 0 0 2.5px 0;
      font-size: 12px;
      color: grey;
      word-break: break-all;
    }
    .name{
      margin: 0 0 2.5px 0;
    }
  }
}

.card-6{
	position: relative;
	@include flex( flex, column, flex-start, flex-start, flex-start );
	width: 200px;
	height: auto;
	padding: 0 10px;
	margin: 10px 0;
	border-right: 2px solid $grey-background;

	&:last-child{
		border-right: 0;
	}
	.title{
		position: relative;
		padding: 0;
		margin: 0;
		color: $primary-color;
	}
	.paragraph{
		 @extend %paragraph;
		 font-size: 12px;
		 line-height: normal;
	}
}

.bold{
  font-weight: bold;
}

.nav-tabs-1{
  position: relative;
  display: block;

  .links{
    margin-bottom: 10px;
    border-bottom: 2px solid whitesmoke;
    @include flex( flex, row, center,center, center  );
    .btn{
      @include btn( null,  transparent, transparent, 5px 5px 0 0 );
      margin: 0 -5px -2px 0;
      border-width: 2px;
      border-bottom-color:$primary-color;
      color: $primary-color;
      &.active{
        background: $primary-light-color;
      }
    }
  }
}

.form-1{
  position: relative;
  width: 400px;
	max-width: 100%;
  .field{
    margin: 10px 0;
    &:first-child{
      margin-top: 25px;
    }
    &.last{
      margin-bottom: 25px;
    }
    .error{
      padding: 5px 0 0 0;
      margin: 0;
      color: #d93025;
    }
    a, .info{
      position: relative;
      display: block;
      text-align: right;
      padding: 5px 0 0 0;
      font-size: 14px;
      color: $primary-color;
      text-decoration: none;
      &:hover:not(.info){
        text-decoration: underline;
      }
    }
  }

}

.dropdown{
	position: relative;
	display: block;
	width: auto;
	overflow: visible;
  z-index: 2;
	.options{
		position: absolute;
		width: max-content;
		background: white;
		border-radius: 5px;
		animation: fadeInUp .2s;
		box-shadow: $lg-shadow;
    right: 0;
    top: calc(100% + 10px);
    padding: 10px 0;
    a{
      position: relative;
      text-decoration: none;
      font-weight: bold;
      font-size: 14px;
      letter-spacing: .5px;
      padding: 10px 20px;
      box-sizing: border-box;
      color: black;
      border-bottom: 1px solid whitesmoke;
      justify-content: flex-start;
      width: 100%;
      i{
        margin-right: 20px;
        color: grey;
      }
      &:last-child{
        border: 0;
      }

      &:hover{
        background: #f9f9f9;
      }
      &:active, &.active{
        background: whitesmoke;
      }
    }
		&.horz{
			@include flex( flex, row, flex-start, flex-start, flex-start );

		}
		&.vert{
			@include flex( flex, column, flex-start, flex-start, flex-start );

		}
    .title{
      position: relative;
      display: block;
      width: 100%;
      text-align: center;
      box-sizing: border-box;
      margin: 0;
      padding: 10px 0;
      font-size: 16px;
    }
		&:before{
			content: ' ';
			position: absolute;
			top: -20px;
			height: 20px;
			width: 100%;
			background: transparent;
		}
 }


	&:not(:hover){
		.options{
			display: none;
		}
	}
}

